.Calendar body,
.Calendar h1,
.Calendar h2,
.Calendar h3,
.Calendar h4,
.Calendar h5,
.Calendar h6,
.Calendar hr,
.Calendar p,
.Calendar blockquote,
.Calendar dl,
.Calendar dt,
.Calendar dd,
.Calendar ul,
.Calendar ol,
.Calendar li,
.Calendar pre,
.Calendar form,
.Calendar fieldset,
.Calendar legend,
.Calendar button,
.Calendar input,
.Calendar textarea,
.Calendar th,
.Calendar td {
  margin: 0;
  padding: 0;
}
.Calendar body,
.Calendar button,
.Calendar input,
.Calendar select,
.Calendar textarea {
  font: 12px/1.5tahoma, arial, \5b8b\4f53;
}
.Calendar h1,
.Calendar h2,
.Calendar h3,
.Calendar h4,
.Calendar h5,
.Calendar h6 {
  font-size: 100%;
}
.Calendar address,
.Calendar cite,
.Calendar dfn,
.Calendar em,
.Calendar var {
  font-style: normal;
}
.Calendar code,
.Calendar kbd,
.Calendar pre,
.Calendar samp {
  font-family: couriernew, courier, monospace;
}
.Calendar small {
  font-size: 12px;
}
.Calendar ul,
.Calendar ol {
  list-style: none;
}
.Calendar a {
  text-decoration: none;
}
.Calendar a:hover {
  text-decoration: underline;
}
.Calendar sup {
  vertical-align: text-top;
}
.Calendar sub {
  vertical-align: text-bottom;
}
.Calendar legend {
  color: #000;
}
.Calendar fieldset,
.Calendar img {
  border: 0;
}
.Calendar button,
.Calendar input,
.Calendar select,
.Calendar textarea {
  font-size: 100%;
}
.Calendar table {
  border-collapse: collapse;
  border-spacing: 0;
}
